.items-position {
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
}
.carousel-span {
  top: 50%;
  font-size: 18px;
}
.carousel-title {
  top: 30%;
}
.carousel-button {
  color: #fff;
  top: 80%;
  text-decoration: none !important;
  border: 1.8px solid rgb(255, 252, 252);
  padding: 4px 15px;
  border-radius: 4px;
  font-weight: 600;
}

.carousel-button:hover {
  background: #fff;
  color: #000 !important;
  text-shadow: none;
}

.carousel-text-shadow {
  text-shadow: 0px 1.8px 2px rgba(0, 0, 0, 0.7);
}

.carousel-fade .carousel-item {
  display: block;
  opacity: 0;
  transition: opacity 0.75s ease-in-out;
}

.carousel-fade .carousel-item.active {
  opacity: 1;
}

.carousel-size {
  width: 75%;
}
@media only screen and (max-width: 750px) {
  .carousel-size {
    width: 100%;
  }
}
.filter-image {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
}

.carousel-item > img {
  width: 100%;
}
